/* eslint-disable no-undef */
<template>
  <div class="wrapper">
      <div class="username" >
          <b-icon @click="Back" icon="arrow-left-square-fill" variant="primary" scale="2"></b-icon>
          <h3 class="mr-5 name">@{{email.split('@')[0]}}</h3>
      </div>
      <div class="user-info">
           <b-avatar v-if="!imageData" :src="`http://18.208.165.238:6200/${form.image}`" size="6rem"></b-avatar>
           <b-avatar v-else  :src="imageData" size="6rem"></b-avatar>
           <form @submit.prevent="submitImage" >
            <input @change="previewImage" ref="file" type="file">
              <button  type="submit" class="btn btn-primary" v-if="imageData">save</button>
              <span @click="changeimage" class="btn btn-secondary ml-2" v-if="imageData">change</span>
           </form>
              <button v-if="!imageData" @click="changeimage" class="btn btn-change"><span class="text-white">change</span></button>
          <h3  class="font-weight-bold">{{form.username}}</h3>
          <span class="text-secondary">@{{email.split('@')[0]}}</span>
      </div>
      <div class="acount-info mt-3" >
          <h3 style="font-style: italic;">account</h3>
          <div data-toggle="modal" data-target="#exampleModal">
              <span>{{form.username}}</span>
              <br>
              <span v-b-modal.modal-center data-toggle="modal" data-target="#exampleModal"  class="text-primary">tap to change info-profile</span>
          </div>
          <hr>
          <Modal />
      </div>
      <div class="acount-info mt-3" >
          <h5 style="font-style: italic;">i'm senior backend developer at microsoft</h5>
          <div>
              <span class="text-secondary">Bio</span>
              <br>
              <span class="text-primary">tap to change bio</span>
          </div>
          <hr>
      </div>
      <div class="mt-2 icon-setting" >
          <h3 class="font-weight-bold mt-3">settting</h3>
          <div class="item-setting mt-3">
             <b-icon  icon="bell-fill" variant="primary" scale="2"></b-icon>
             <h4 class="ml-4 text-primary">notification</h4>
          </div>
          <div class="item-setting mt-3">
             <b-icon  icon="lock-fill" variant="primary" scale="2"></b-icon>
             <h4 class="ml-4 text-primary">Private and Security </h4>
          </div>
          <div class="item-setting mt-3">
             <b-icon  icon="clipboard-data" variant="primary" scale="2"></b-icon>
             <h4 class="ml-4 text-primary">Data and Storage</h4>
          </div>
          <div class="item-setting mt-3">
             <b-icon  icon="chat-left-text" variant="primary" scale="2"></b-icon>
             <h4 class="ml-4 text-primary">Chat Setting</h4>
          </div>
          <div class="item-setting mt-3">
             <b-icon  icon="phone-vibrate" variant="primary" scale="2"></b-icon>
             <h4 class="ml-4 text-primary">Device</h4>
          </div>
          <div class="item-setting mt-3" @click="OnLogout">
             <b-icon  icon="door-open" variant="danger" scale="2"></b-icon>
             <h4 class="ml-4 text-danger">Logout</h4>
          </div>
      </div>
  </div>
</template>

<script>
import Modal from '../components/Modal'
import { mapActions } from 'vuex'
export default {
  components: {
    Modal
  },
  data () {
    return {
      imageData: '',
      imageChanged: '',
      email: localStorage.getItem('email'),
      form: {
        username: '',
        email: '',
        alamat: '',
        phone_number: '',
        country: '',
        address: '',
        files: '',
        image: ''
      }
    }
  },
  methods: {
    ...mapActions({
      dataAlls: 'profile/getAlluser',
      updateData: 'profile/updateUser'
    }),
    Back () {
      this.$router.push({ name: 'content' })
    },
    changeimage () {
      this.$refs.file.click()
    },
    submitImage () {
      const formdata = new FormData()
      formdata.append('username', this.form.username)
      formdata.append('alamat', this.form.alamat)
      formdata.append('phone_number', this.form.phone_number)
      formdata.append('country', this.form.country)
      formdata.append('address', this.form.address)
      formdata.append('image', this.imageChanged)
      this.updateData(formdata).then(dt => {
        window.location.reload()
        this.$toast.success('berhasil update image')
        setTimeout(() => {
          window.location.reload()
        }, 1000)
      }).catch(dt => {
        console.log(dt)
        alert(dt)
      })
    },
    previewImage (event) {
      this.imageChanged = event.target.files[0]
      this.imageData = ''
      // Reference to the DOM input element
      var input = event.target
      // Ensure that you have a file before attempting to read it
      if (input.files && input.files[0]) {
        // create a new FileReader to read this image and convert to base64 format
        var reader = new FileReader()
        // Define a callback function to run, when FileReader finishes its job
        reader.onload = (e) => {
          // Note: arrow function used here, so that "this.imageData" refers to the imageData of Vue component
          // Read image as base64 and set to imageData
          this.imageData = e.target.result
        }
        // Start the reader job - read file as a data url (base64 format)
        reader.readAsDataURL(input.files[0])
      }
    },
    OnLogout () {
      localStorage.removeItem('token')
      localStorage.removeItem('username')
      localStorage.removeItem('id')
      window.location = '/login'
    }
  },
  mounted () {
    this.dataAlls().then(data => {
      this.allData = data
      this.form.username = data[0].username
      this.form.email = data[0].email
      this.form.phone_number = data[0].phone_number
      this.form.country = data[0].country
      this.form.alamat = data[0].alamat
      this.form.image = data[0].image
    })
  }

}
</script>

<style scoped>
.btn-change {
  width: 30%;
  position: absolute;
}
.btn-change span {
  font-weight: bold;
}
input[type="file"] {
  display: none;
}
.wrapper {
    padding: 30px;
    overflow-x: hidden !important;
}
.name {
    margin-right: 200px;
}
.username {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-top: 30px;
}
.user-info {
    margin-top: 30px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}
.item-setting {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.icon-setting {
   padding: 10px;
   cursor: pointer;
}
.circular--portrait {
   position:relative;
  width:20%;
  padding-bottom:20%;
  border-radius:50%;
  overflow:hidden;
}
.circular--portrait img {
 position:absolute;
  object-fit: cover;
  width:100%;height:100%;
}

</style>
