<template>
<b-modal hide-header hide-footer id="modal-center" centered title="BootstrapVue">
  <form @submit.prevent="editdata" >
  <div class="form-group">
    <label for="exampleInputEmail1">username</label>
    <input type="text" v-model="form.username" class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="uername">
  </div>
  <div class="form-group">
    <label for="exampleInputEmail1">email</label>
    <input type="email" readonly v-model="form.email" class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="email">
  </div>
  <div class="form-group">
    <label for="exampleInputEmail1">alamat</label>
    <input type="text" v-model="form.alamat" class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="alamat">
  </div>
  <div class="form-group">
    <label for="exampleInputEmail1">phone number</label>
    <input type="number"  v-model="form.phone_number" class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="phone number">
  </div>
  <div class="form-group">
    <label for="exampleInputEmail1">country</label>
    <input type="text" v-model="form.country" class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="country">
  </div>
  <button type="submit" class="btn btn-primary">save</button>

  </form>
  </b-modal>
</template>

<script>
import { mapActions } from 'vuex'
export default {
  methods: {
    ...mapActions({
      dataAll: 'profile/getAlluser',
      updateData: 'profile/updateUser'
    }),
    editdata () {
      this.dataAll()
      const formdata = new FormData()
      // console.log(this.form.files)
      formdata.append('username', this.form.username)
      formdata.append('alamat', this.form.alamat)
      formdata.append('phone_number', this.form.phone_number)
      formdata.append('country', this.form.country)
      formdata.append('address', this.form.address)
      formdata.append('image', this.form.files)
      this.updateData(formdata).then(dt => {
        this.$toast.success('success changes or update info profile')
        setTimeout(() => {
          window.location.reload()
        }, 1000)
      }).catch(dt => {
        console.log(dt)
        alert(dt)
      })
    }
  },
  data () {
    return {
      form: {
        username: '',
        email: '',
        alamat: '',
        phone_number: '',
        country: '',
        address: '',
        files: '',
        image: ''
      }
    }
  },
  mounted () {
    this.dataAll().then(data => {
      this.allData = data
      this.form.username = data[0].username
      this.form.email = data[0].email
      this.form.phone_number = data[0].phone_number
      this.form.country = data[0].country
      this.form.alamat = data[0].alamat
      this.form.image = data[0].image
    })
  }
}
</script>

