import axios from 'axios'

const state = () => {
  return {
    id: localStorage.getItem('id'),
    friendPage: '',
    add: '',
    frienddb: ''
  }
}
const getters = {
  displayFriend: (state) => state.friendPage,
  getDatadd: (state) => state.add,
  getdbFriend: (state) => state.frienddb
}
const mutations = {
  DISPLAY (state, data) {
    state.friendPage = data
  },
  ADD (state, data) {
    state.add = data
  },
  GET_ALL_DB (state, data) {
    console.log(data)
    state.frienddb = data
  },
  COUNT (state, data) {
    console.log(data)
  }
}

const actions = {
  friendDisplay ({ commit }, data) {
    commit('DISPLAY', data)
  },
  friendAdd ({ commit }, data) {
    commit('ADD', data)
  },
  getAllfrienddb ({ commit }, data) {
    return new Promise((resolve, reject) => {
      axios.get(`http://18.208.165.238:6200/user/data/displayfriend/${data}`).then(ress => {
        commit('GET_ALL_DB', ress.data)
      })
    })
  },
  editStatusFriend ({ commit }, data) {
    return new Promise((resolve, reject) => {
      axios.put(`http://18.208.165.238:6200/user/friend/updateFriend/${data.id}`).then(ress => {
        console.log(ress.data.message)
      })
    })
  },
  getCount ({ commit }, data) {
    return new Promise((resolve, reject) => {
      axios.get(`http://18.208.165.238:6200/user/friend/count/${data}`).then(ress => {
        resolve(ress.data.data[0].jml)
        commit('COUNT', ress.data.data[0].jml)
      })
    })
  },
  getCountActive ({ commit }, data) {
    return new Promise((resolve, reject) => {
      axios.get(`http://18.208.165.238:6200/user/friend/countActive/${data}`).then(ress => {
        resolve(ress.data.data[0].jml)
      })
    })
  }
}

export default {
  namespaced: true,
  state,
  actions,
  mutations,
  getters
}
