<template>
 <div>
  <b-card no-body>
    <b-tabs card>
      <b-tab title="friends" active>
           <template v-slot:title>
            <b-icon icon="person-check-fill" />
            {{ CountActive}}
          </template>
        <b-card-text>
            <div v-for="(item, index) in getdb" :key="index">
              <div v-if="item.status === 1 " class="friend mt-4">
                    <b-avatar variant="info" :src="`http://18.208.165.238:6200/${item.image}`"></b-avatar>
                <h4>{{item.friend}}</h4>
                <b-button class="btn-sm" variant="success">
                     <b-icon icon="check2-circle"></b-icon>
                     Friends
                </b-button>
              </div>
            </div>
        </b-card-text>
      </b-tab>
      <b-tab title="request" icon="gear">
           <template v-slot:title>
            <b-icon icon="hourglass-split" />
            <span>{{Count}}</span>
          </template>
        <b-card-text>
            <div v-for="(item, index) in getdb" :key="index">
              <div v-if="item.status === 0 " class="friend mt-4">
                    <b-avatar variant="info" :src="`http://18.208.165.238:6200/${item.image}`"></b-avatar>
                <h4>{{item.friend}}</h4>
                <div class="button-accept" v-if=" item.action === 1">
                 <b-button @click="diterima(item)" class="btn-sm mr-4" variant="success">
                     <b-icon icon="check-square"></b-icon>
                </b-button>
                 <b-button @click="ditolak()" class="btn-sm" variant="danger">
                     <b-icon icon="x"></b-icon>
                </b-button>
                </div>
                <div v-else-if="item.action === 0 || item.status === 1 ">
                 <b-button class="btn-sm" variant="secondary">
                     <b-icon icon="stopwatch-fill"></b-icon>
                     <small>waiting</small>
                </b-button>
                </div>
              </div>
            </div>
        </b-card-text>
      </b-tab>
      <b-tab title="decline">
           <template v-slot:title>
            <b-icon icon="person-x-fill" />
          </template>
        <b-card-text>
            <div v-for="(item, index) in getdb" :key="index">
              <div v-if="item.status === 3" class="friend mt-4">
                    <b-avatar variant="info" :src="`http://18.208.165.238:6200/${item.image}`"></b-avatar>
                <h4>{{item.friend}}</h4>
                 <b-button class="btn-sm" variant="danger">
                     <b-icon icon="x"></b-icon>
                     rejected
                </b-button>
              </div>
            </div>
        </b-card-text>
      </b-tab>
    </b-tabs>
  </b-card>
</div>
</template>

<script>
import io from 'socket.io-client'
import { mapActions, mapGetters } from 'vuex'
export default {
  data () {
    return {
      searchResult: [],
      username: localStorage.getItem('username'),
      id_user: localStorage.getItem('id'),
      socket: io('http://18.208.165.238:6200'),
      jml: [],
      Count: '',
      CountActive: ''
    }
  },
  methods: {
    ...mapActions({
      status: 'addFriend/editStatusFriend',
      getCount: 'addFriend/getCount',
      getCountActive: 'addFriend/getCountActive'
    }),
    diterima (item) {
      this.status(item)
      this.socket.emit('edit-status', item)
      this.Count = this.Count - 1
      window.location.reload()
    },
    ditolak () {
      alert('ditolak')
    }

  },
  mounted () {
    this.getCountActive(this.id_user).then(res => {
      this.CountActive = res
    })
    this.getCount(this.id_user).then(ress => {
      console.log(ress)
      this.Count = ress
      this.$toast.success(`Welcome to friend section you have ${this.Count} request friend `)
    })
    this.socket.on('accept-request', data => {

    })
    this.socket.on('send-search-result', asu => {
      console.log(asu)
      this.searchResult = asu
    })
    this.socket.emit('send-id', {
      id: this.id_user,
      friends: this.username
    })
  },
  computed: {
    ...mapGetters({
      friendss: 'addFriend/displayFriend',
      reqFreind: 'addFriend/getDatadd',
      getdb: 'addFriend/getdbFriend'
    }),
    getjmlnotif () {
      return this.Count ? this.Count - 1 : this.Count
    }
  }

}
</script>

<style scoped>
.button-accept {
  display: flex;
  justify-content: space-evenly;
}
.friend {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center ;
}

</style>
