import axios from 'axios'

const state = () => {
  return {
    id: localStorage.getItem('id'),
    profile: ''
  }
}
const getters = {
  profileFriendinfo: (state) => state.profile
}
const mutations = {
  FRIEND_PROFILE (state, data) {
    state.profile = data
  }
}

const actions = {
  getAlluser ({ state }) {
    console.log()
    return new Promise((resolve, reject) => {
      axios.get(`${process.env.VUE_APP_API_URL}/user/${state.id}`).then((ress) => {
        // console.log(ress.data.data)
        resolve(ress.data.data)
      })
    })
  },
  updateUser ({ state }, payload) {
    return new Promise((resolve, reject) => {
      axios.put(`${process.env.VUE_APP_API_URL}/user/updateUser/${state.id}`, payload).then(ress => {
        if (ress.data.error === 'Error: Images Only !!!') {
          resolve(ress.data.error)
        } else {
          resolve('berhasil sob')
        }
        console.log(ress.data)
      }).catch(err => {
        reject(err)
      })
    })
  },
  profileFriend ({ commit }, id) {
    return new Promise((resolve, reject) => {
      axios.get(`http://18.208.165.238:6200/user/${id}`).then(ress => {
        commit('FRIEND_PROFILE', ress.data.data)
      })
    })
  }
}

export default {
  namespaced: true,
  state,
  actions,
  mutations,
  getters
}
