<template>
  <div class="content-wrap">
      <div class="row">
          <div style="border: 1px solid green" class="col-md-4 list-chat">
              <nav class="mt-4">
                  <div  class="logo">
                     <h4 class="text-primary"> TakaApp   </h4>
                  </div>
                      <div @click="display" style="cursor: pointer" class="mr-2">
                           <b-icon icon="card-text" scale="3" variant="primary"></b-icon>
                      </div>
                  <ul :class="{onClicklogo: slide}" >
                    <div class="profil-section-info mr-5">
                      <div @click="display"><b-icon icon="arrow-right-circle" variant="primary" scale="2"></b-icon></div>
                      <div @click="userInfo"><b-icon icon="gear" variant="primary" scale="2"></b-icon></div>
                    </div>
                    <div class="profil-section-image">
                       <img style=""  :src="`http://18.208.165.238:6200/${getInfouser.image}`" width="90" alt="">
                       <h5 class="mt-3 text-primary">{{getInfouser.username}}</h5>
                       <form @submit.prevent="search_user">
                          <input v-model="searchMessage" type="text" placeholder="search friend">
                       </form>
                       <small v-if="listsearchMEssage.status === 'gagal'" class="text-danger">user not found</small>
                       <small v-else-if="listsearchMEssage.email === email_user" class="text-danger"> {{listsearchMEssage.name}} telah ditambahkan</small>
                       <small v-else-if="getResultsame" class="text-danger"> {{listsearchMEssage.name}} telah ditambahkan</small>
                       <div   v-else-if="listsearchMEssage.status === 'sukses' " class=" hayuk text-success">
                         <small class="text-success">{{listsearchMEssage}} ingin ditambahkan?</small>
                         <button @click="addSearch(listsearchMEssage.data)" class="btn btn-success mt-2">add</button>
                       </div>
                       <small v-else class="text-secondary">search to add friend</small>
                    </div>
                    <div>
                      <h4>{{username}}</h4>
                    </div>
                      <li>
                        <span><b-icon icon="person-plus" scale="2" variant="primary"></b-icon></span>
                        <span class="text-primary ml-3">Invite Friend</span>
                      </li>
                      <li>
                        <span><b-icon icon="question-circle" scale="2" variant="primary"></b-icon></span>
                        <span class="text-primary ml-3">Telegram Faq</span>
                      </li>
                      <li>
                        <span><b-icon icon="people" scale="2" variant="primary"></b-icon></span>
                        <span class="text-primary ml-3">Contact</span>
                      </li>
                      <li>
                         <span><b-icon icon="telephone" scale="2" variant="primary"></b-icon></span>
                        <span class="text-primary ml-3">Call</span>
                      </li>
                      <li @click="maps">
                        <span><b-icon icon="map" scale="2" variant="primary"></b-icon></span>
                        <span class="text-primary ml-3">Map</span>
                      </li>
                      <li @click="onLogout">
                        <span><b-icon icon="door-open-fill" scale="2" variant="danger"></b-icon></span>
                        <span class="text-danger ml-3">Logout</span>
                      </li>
                  </ul>
              </nav>
              <div  class="search  mt-3">
                    <div class="form-group has-search">
                        <span class=" icon-search fa fa-search form-control-feedback"></span>
                        <input  type="text" class=" search-input form-control mt-2" placeholder="Search">
                    </div>
                  <div  class="i-plus">
                       <b-icon icon="plus" scale="3" variant="primary"></b-icon>
                  </div>
              </div>
              <div class="type" style="cursor: pointer">
                  <h5 class="btn" :class="{'btn-primary d-inline-block btn rounded-pill mr-2' : getColor}"   @click="routeDashboard">Dashboard</h5>
                  <h5 class="btn" :class="{'btn-primary d-inline-block btn rounded-pill mr-2' : getColorGroup}" @click="routeGroup"> group</h5>
                  <h5 class="btn" :class="{'btn-primary d-inline-block btn rounded-pill mr-2  ' : getColorfriend}" @click="routeFreind">friend</h5>
                  <h5 :class="{'btn-primary d-inline-block btn rounded-pill mr-2  ' : getColorSetting}" @click="routeSetting"  class="btn">setting</h5>
              </div>
              <div v-if="getCurrentRoute === 'content'">
              <div v-for="(item,index) in getdb" :key="index" >
                  <div   v-if="username !== item.username && item.status === 1" class="people-chat mt-4">
                  <div @click="imageFriend(item)" class="image-chat">
                       <img style=""  :src="`http://18.208.165.238:6200/${item.image}`" width="90">
                  </div>
                  <div  @click="userlist(item)" class="people-list">
                      <p class="font-weight-bold">{{item.friend}}</p>
                      <p v-if="chatNOtif.sender === item.friend" class="text-primary">{{chatNOtif.message}}</p>
                      <p v-else class="text-primary"></p>
                  </div>
                  <div  class="people-list">
                      <p>19.40</p>
                      <span v-if="!chatNOtif.jml"></span>
                      <span  v-else-if=" chatNOtif.sender === item.friend && dataasu">{{chatNOtif.jml.length}}</span>
                      <span  v-else-if=" chatNOtif.receiver === item.user">{{chatNOtif.jml.lenth}}</span>
                      <!-- <span v-else>su</span> -->
                  </div>
                  </div>
              </div>
                  <div class="text-center"  v-if="getdb.length === 0">
                    <div class="d-flex mt-5 justify-content-center align-content-center">
                      <img  width="50%" height="40%" src="https://img.pngio.com/chat-png-icon-chat-vector-icon-png-4964001-pngtube-chat-icon-png-920_970.png" alt="">
                      <h4 class="text-secondary">you must be add friend or accept friend to start chatting</h4>
                    </div>
                  </div>
              </div>
               <router-view  />
          </div>
          <div class="col-md-8 chat-content" v-if="userReceiver === null" style="border: 1px solid blue" >
            please select a chat to start messaging
          </div>
          <div v-else class="col-md-8 contentchat " :class="{ 'content-chat2': hasdisplay }" >
               <div  class="info-content">
                <div class="info-person">
                    <img style="width: 90px"   :src="`http://18.208.165.238:6200/${imageChanged}`" alt="">
                    <div v-b-toggle.sidebar-right class="info-online ml-3">
                        <span class="font-weight-bold">{{userReceiver}}</span>
                        <span v-if="status.status === 'Connected' && userReceiver === status.onlineUSer">online</span>
                        <span v-else>offline</span>
                      <div class="">
                        <VEmojiPicker  @select="selectEmoji" />
                    </div>
                    </div>
                </div>
                <div @click="userlist">
                  <b-icon icon="arrows-fullscreen" scale="1" class="ml-3" ></b-icon>
                </div>
            </div>
            <div  class="body-chat"
             v-chat-scroll="{
                  always: true,
                  smooth: true,
                  scrollonremoved: true,
                  smoothonremoved: true
                }"
            >
                <div v-for="(item,index) in historyMessage" :key="index" class="tayo-chat">
                <div v-if="username !== item.sender" class="chat-sender">
                   <div>
                        <img src="../assets/assets/img/Rectangle 3.png" alt="">
                   </div>
                   <div class="text-reeiver">
                      <p>{{item.message}}
                        <span>
                    <timeago
                      :datetime="item.created_at"
                      style="font-size: 10px"
                      class="font-12 d-flex font-weight-bold"
                      :auto-update="60"
                    ></timeago>
                        </span>
                        <span style="font-size: 10px">{{new Date(item.created_at).toLocaleTimeString()}}</span>
                      </p>
                   </div>
               </div>
                <div class="chat-sender" v-else>
                   <div>
                        <img class="right" src="../assets/assets/img/Rectangle 3.png" alt="">
                   </div>
                   <div class="text-sender">
                      <p>{{item.message}}
                        <span class="d-flex time-sender">
                        <timeago
                        style="font-size: 10px"
                        :datetime="item.created_at"
                        class="text-white font-3"
                        :auto-update="60"
                      ></timeago>
                        </span>
                        <span style="font-size: 10px">{{new Date(item.created_at).toLocaleTimeString()}}</span>
                      </p>
                   </div>
               </div>
               </div>
               <div v-if="userReceiver !== null ">
                 <div v-for="(item,index) in privatemessage" :key="index" class="tayo-chat">
                <div v-if="username !== item.sender" class="chat-sender">
                   <div>
                        <img src="../assets/assets/img/Rectangle 3.png" alt="">
                   </div>
                   <div class="text-reeiver">
                      <p>{{item.message}}
                        <span>
                          <timeago
                            :datetime="getDateNow"
                            style="font-size: 10px"
                            class="font-12 d-flex font-weight-bold"
                            :auto-update="60"
                          ></timeago>
                        </span>
                        <span style="font-size: 10px">
                          {{new Date(getDateNow).toLocaleTimeString()}}
                        </span>
                      </p>
                   </div>
               </div>
                <div class="chat-sender" v-else>
                   <div>
                        <img class="right" src="../assets/assets/img/Rectangle 3.png" alt="">
                   </div>
                   <div class="text-sender">
                      <p>{{item.message}}
                        <span>
                          <timeago
                            :datetime="getDateNow"
                            style="font-size: 10px"
                            class="font-12 d-flex text-white font-weight-bold"
                            :auto-update="60"
                          ></timeago>
                        </span>
                        <span style="font-size: 10px">
                          {{new Date(getDateNow).toLocaleTimeString()}}
                        </span>
                      </p>
                   </div>
               </div>
               </div>
               </div>
            </div>
            <div  class="send-chat">
               <form @submit.prevent="onSend(0)">
                    <input v-model="message" class="input-send" type="text" placeholder="ketik disini....">
               </form>
                <div  class="icon-send">
                    <b-icon class="ml-3" icon="plus" scale="2" ></b-icon>
                     <b-icon @click="handleShowEmoji" icon="emoji-smile" class="ml-3" scale="1" ></b-icon>
                     <b-icon icon="images" scale="1" class="ml-3" ></b-icon>
                </div>
            </div>
          </div>
      </div>
      <Sidebar />
  </div>
</template>

<script>
import io from 'socket.io-client'
import axios from 'axios'
import Sidebar from '../components/sidebarRight'
import { mapActions, mapGetters } from 'vuex'
import VEmojiPicker from 'v-emoji-picker'
export default {
  components: {
    Sidebar,
    VEmojiPicker
  },
  data () {
    return {
      slide: true,
      email_user: localStorage.getItem('email'),
      socket: io('http://18.208.165.238:6200'),
      alluser: [],
      message: '',
      chatNOtif: '',
      username: localStorage.getItem('username'),
      userReceiver: null,
      listmessage: [],
      privatemessage: [],
      historyMessage: [],
      id_user: localStorage.getItem('id'),
      searchMessage: '',
      listsearchMEssage: [],
      id_friend: null,
      sultsult: [],
      addresukt: [],
      getInfouser: {},
      hasdisplay: false,
      hasnodisplay: true,
      dataasu: [],
      bismillah: '',
      imageChanged: '',
      status: '',
      is_Online: localStorage.getItem('online'),
      getColor: false,
      getColorfriend: false,
      getColorGroup: false,
      getColorSetting: false,
      showEmoji: false
    }
  },
  methods: {
    handleShowEmoji () {
      this.showEmoji = !this.showEmoji
    },
    selectEmoji (emoji) {
      this.message += emoji.data
    },
    ...mapActions({
      displayPageFrind: 'addFriend/friendDisplay',
      addFriends: 'addFriend/friendAdd',
      getAllFriend: 'addFriend/getAllfrienddb',
      getProfile: 'profile/profileFriend'

    }),
    routeSetting () {
      this.$router.push({ name: 'edit' })
      this.getColorSetting = !this.getColorSetting
      this.getColor = false
      this.getColorGroup = false
      this.getColorfriend = false
    },
    routeDashboard () {
      this.$router.push({ name: 'content' })
      this.getColor = !this.getColor
      this.getColorfriend = false
      this.getColorGroup = false
      this.getColorSetting = false
      this.getAllFriend(this.id_user)
    },
    routeGroup () {
      this.getColorGroup = !this.getColorGroup
      this.getColor = false
      this.getColorfriend = false
      this.getColorSetting = false
      this.$router.push({ name: 'group' })
    },
    routeFreind () {
      this.$router.push({ name: 'friend' })
      this.getColorfriend = !this.getColorfriend
      this.getColor = false
      this.getColorGroup = false
      this.getColorSetting = false
      this.getAllFriend(this.id_user)
    },
    imageFriend (params) {
      this.$router.push({ path: 'friendmap', query: { id: params.idfriend } })
    },
    maps () {
      this.$router.push({ path: '/map' })
    },
    display () {
      this.slide = !this.slide
    },
    displayallfriend () {
      axios.get(`http://18.208.165.238:6200/user/data/displayfriend/${this.id_user}`).then(dt => {
        this.addresukt = dt.data
      })
    },
    userInfo () {
      this.$router.push({ name: 'edit' })
    },
    getidUser () {
      axios.get(`http://18.208.165.238:6200/user/${this.id_user}`).then(ress => {
        this.getInfouser = ress.data.data[0]
      })
    },
    search_user () {
      const data = {
        email: this.searchMessage
      }
      axios.post('http://18.208.165.238:6200/user/email/getemail', data).then(ress => {
        // console.log(ress.data)
        this.listsearchMEssage = ress.data
      })
    },
    addSearch (data) {
      const friend = data[0].username
      const id = parseInt(this.id_user)
      const friendId = data[0].id
      const insert = {
        id_friends: friendId
      }
      this.socket.emit('freind-request', {
        user: this.username,
        friend: friend,
        id: id,
        id_freind: friendId
      })
      axios.post(`http://18.208.165.238:6200/user/addfriend/${this.id_user}`, insert).then(dt => {
        window.location = '/'
      })
    },
    onSend (params) {
      this.socket.emit('statusConnected', this.username)
      this.listmessage = [...this.listmessage, {
        sender: this.username,
        receiver: this.userReceiver,
        message: this.message
      }]
      this.dataasu = [...this.dataasu, this.historyMessage]
      // eslint-disable-next-line no-unreachable
      this.socket.emit('send-message', {
        sender: this.username,
        receiver: this.userReceiver,
        message: this.message,
        jml: this.dataasu
      })
      this.message = ''
      if (this.userReceiver !== null) {
        this.getprivatmessage()
      }
    },
    userlist (user) {
      console.log(user)
      this.getProfile(user.idfriend)
      this.imageChanged = user.image
      this.chatNOtif = []
      this.dataasu = []
      // this.chatNOtif.jml = []
      this.hasnodisplay = !this.hasnodisplay
      this.hasdisplay = !this.hasdisplay
      this.userReceiver = user.friend
      this.listmessage = []
      this.privatemessage = []
      this.getprivatmessage()
      // this.socket.emit('statusConnected', this.userReceiver)
      this.socket.emit('get-hisyory-message', {
        sender: this.username,
        receiver: this.userReceiver,
        message: this.message,
        image: this.getInfouser.image
      })
    },
    getprivatmessage () {
      const asu = this.listmessage.filter((item) => {
        if (this.userReceiver === null) {
          return item.sender === this.userReceiver || item.sender === this.username
        } else {
          return item.receiver === this.userReceiver || item.sender === this.userReceiver
        }
      })
      this.privatemessage = asu
    },
    getprivatmessage2 () {
      const asu = this.listmessage.filter((item) => item.receiver === this.userReceiver || item.receiver === this.username)
      this.privatemessage = asu
    },
    onLogout () {
      localStorage.removeItem('token')
      localStorage.removeItem('username')
      localStorage.removeItem('id')
      window.location = '/'
    }
  },
  mounted () {
    this.socket.on('status-active', data => {
      this.$bvToast.toast('request friend accepted notification, please check dashboard section', {
        title: `you have been accepted  ${data.user}`,
        solid: true,
        autoHideDelay: 10000,
        href: '/content'
      })
    })
    this.getidUser()
    this.getAllFriend(this.id_user)
    this.socket.emit('statusConnected', this.username)
    this.socket.on('connected', data => {
      this.status = data
    })
    this.socket.on('accept-request', data => {
      this.addFriends(data)
      // this.$toast.success(`you have a request friend from ${data.friend}`)
      this.$bvToast.toast('request friend notification, please check friend section', {
        title: `you have a request friend from ${data.friend}`,
        solid: true,
        autoHideDelay: 10000
      })
      console.log(data)
    })
    // this.displayallfriend()
    this.socket.on('send-search-result', data => {
      this.sultsult = data
      this.displayPageFrind(this.sultsult)
    })
    this.socket.on('send-history-message', data => {
      // console.log(data)
      this.historyMessage = data
    })
    this.socket.emit('send-data', 'hai manis')
    this.socket.emit('send-id', {
      id: this.id_user,
      friend: this.username
    })
    this.socket.on('ganteng', data => {
      this.alluser = data
    })
    this.socket.on('asu', async data => {
      this.chatNOtif = data
      this.listmessage = [...this.listmessage, data]
      if (this.userReceiver !== null) {
        this.getprivatmessage()
      }
    })
    this.socket.emit('join-room', {
      user: this.username
    })
  },
  computed: {
    ...mapGetters({
      getdb: 'addFriend/getdbFriend'
    }),
    getDateNow () {
      const date = new Date().toJSON()
      return date
    },
    getCurrentRoute () {
      return this.$route.name
    },
    getResultsame () {
      return this.sultsult.some((item) => item.emailFreind === `${this.listsearchMEssage.email}`)
    }
  }

}
</script>

